import React, {Component} from 'react';
import {Link, StaticQuery, graphql} from "gatsby";
import { Scrollbar } from 'react-scrollbars-custom';
import ReactPlayer from 'react-player'
import {API_URL} from "../services/helper";

const MustSeeItem = ({prop}) => {
    return(
        <React.Fragment>
            <StaticQuery query={ graphql`
               {
                  sporttalk{
                      opinions{
                        title
                        body
                        created_at
                        video{
                          url
                        }
                        user{
                          username
                          nickname
                          avatar{
                            url
                          }
                        }
                        poll{
                          id
                          title
                          
                        }
                      }
                }
                }
            `} render={data =>
                <React.Fragment>
                    {data.sporttalk.opinions.map((opinion, key)=>

                        <div key={key} className="must-see-post" style={{display: "flex"}}>
                            <div className="avatar avatar__post">
                                <img src={API_URL + opinion.user.avatar.url} alt="avatar img"/>
                            </div>
                            <div className="info">
                                <div className="must-see-desc">
                                    <div className="name">{opinion.user.username}</div>
                                    <div className="date">
                                        Posted on {opinion.created_at} | Member has 51 opinions and 125.6k opinions views
                                    </div>
                                    <div className="text-post">
                                        {opinion.body}
                                    </div>
                                    {(opinion.video !== null)?
                                        <div className="video-box" style={{
                                            display: "flex",
                                            alignItems: "center",
                                            justifyContent: "center"
                                        }}>
                                            <ReactPlayer url={API_URL + opinion.video.url} controls={true} style={{
                                                display: "flex",
                                                justifyContent: "center",
                                                alignItems: "center"
                                            }} height={108} width={192}/>
                                            {/*<div className="button-play d-flex align-items-center justify-content-center">*/}
                                            {/*    /!*<div className="icon"/>*!/*/}
                                            {/*</div>*/}
                                        </div>
                                        : ''}
                                    <div className="info-bottom " style={{
                                        display: "flex",
                                        justifyContent: "space-between"
                                    }}>
                                        <div className="desc" style={{
                                            display: "flex",
                                            alignItems: "center"
                                        }}>
                                            <div className="desc-item">
                                                <span className="number">1.2k</span>
                                                <span className="text">Votes</span>
                                            </div>
                                            <div className="desc-item">
                                                <span className="number">2.7k</span>
                                                <span className="text">Repies</span>
                                            </div>
                                            <div className="desc-item">
                                                <span className="number">512</span>
                                                <span className="text">Views</span>
                                            </div>
                                        </div>
                                        <div className="btn-wrapp" style={{
                                            display: "flex",
                                            alignItems: "center"
                                        }}>
                                            <Link to={"/"} className="button button__transparent">
                                                <span className="icon icon__arrow-up"/>
                                                <span className="text">Upvote</span>
                                            </Link>
                                            <Link to={"/"} className="button button__transparent">
                                                <span className="icon icon__share"/>
                                                <span className="text">Share</span>
                                            </Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )}
                    {console.log(data.sporttalk.opinions)}
                </React.Fragment>
            } />
        </React.Fragment>
    )
};

class MustSeeItems extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="must-see-responses">
                    <div className="title-h">
                        Must see
                        <span className="color-red">responses</span>
                    </div>
                    <div className="must-see-list">
                        <Scrollbar style={{maxHeight: "1100px", minHeight: "1100px"}} noScrollX >

                            <MustSeeItem/>

                        </Scrollbar>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default MustSeeItems;
