import React, {Component} from 'react';
import "./index.scss";
import {Link} from "gatsby";

class PopularSearch extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="popular-searches">
                    <div className="title title__small">
                        <span className="rectangle rectangle__red"/>Popular searches
                    </div>

                    <div className="btn-wrapp d-flex align-items-center flex-wrap">
                        <Link to={"/"} className="button button__white">
                            <span className="text">Orci varius</span>
                        </Link>
                        <Link to={"/"} className="button button__white">

                            <span className="text">Elementum</span>
                        </Link>
                        <Link to={"/"} className="button button__white">

                            <span className="text">Orci varius</span>
                        </Link>
                        <Link to={"/"} className="button button__white">

                            <span className="text">Suspendisse</span>
                        </Link>
                        <Link to={"/"} className="button button__white">

                            <span className="text">Lorem</span>
                        </Link>
                        <Link to={"/"} className="button button__white">

                            <span className="text">Habitant morbi trist</span>
                        </Link>
                        <Link to={"/"} className="button button__white">

                            <span className="text">Pellentesque</span>
                        </Link>
                        <Link to={"/"} className="button button__white">

                            <span className="text">Cras ultricies</span>
                        </Link>
                    </div>

                    <div className="search">
                        <form action="">
                            <input type="search" className="popular-search" placeholder="Search" />
                                <input type="submit" className="search-icon" value="" />
                        </form>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}


export default PopularSearch;
