import React, {Component} from 'react';
import "./index.scss";
import {Link} from "gatsby";

class Upcoming extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="upcoming">
                    <div className="title title__small">
                        <span className="rectangle rectangle__red"/>Upcoming
                    </div>
                    <Link to={"/"} className="box-link">
                        <div className="list-item-main">
                            <div className="img-box" style={{backgroundImage: "url(../img/upcoming-img.png)"}}>
                                <div className="mask"/>
                            </div>
                            <div className="info d-flex flex-column justify-content-end">
                                <p className="title">
                                    <span className="rectangle rectangle__red">?</span>
                                    Should the Seahawks focus the offense around Russell Wilson instead of the running
                                    game?
                                </p>
                                <div className="desc d-flex align-items-center">
                                    <div className="desc-item">
                                        <span className="number">1.2k</span>
                                        <span className="text">Votes</span>
                                    </div>
                                    <div className="desc-item">
                                        <span className="number">2.7k</span>
                                        <span className="text">Repies</span>
                                    </div>
                                    <div className="desc-item">
                                        <span className="number">512</span>
                                        <span className="text">Views</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Link>
                    <div className="btn-wrapp d-flex">
                        <div className="slider-button slider-button__white d-none d-md-flex align-items-center">
                            <Link to={"/"} className="arrow-link arrow-link__left">
                                <span className="icon-arrow icon-arrow__left" />
                            </Link>
                            <span className="count-slide">0    1/03</span>
                            <Link to={"/"} className="arrow-link arrow-link__right">
                                <span className="icon-arrow icon-arrow__right" />
                            </Link>
                        </div>
                        <div className="slider-dots d-block d-md-none">
                            <Link to={"/"} class="slider-dots-item rectangle active">d</Link>
                            <Link to={"/"} class="slider-dots-item rectangle">d</Link>
                            <Link to={"/"} class="slider-dots-item rectangle">d</Link>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default Upcoming;
